<template>
  <div class="website-detail-container">
    <div class="detail-website">
      <div class="detail-website-top">
        <div class="detail-website-top-img">
          <el-image :src="websiteDetail.logo">
            <template #error>
              <SvgIcon name="list-website" :size="48"/>
            </template>
          </el-image>
        </div>
        <div class="detail-website-top-title">
          <h2>{{ websiteDetail.name }}</h2>
          <p class="detail-website-top-title-url">{{ websiteDetail.url }}</p>
        </div>
        <div class="detail-website-top-more">
          <el-popover
            placement="bottom"
            width="150"
            trigger="hover"
          >
            <VueQrcode
              :options="{ width: 150 }"
              :value="websiteDetail.url"
            ></VueQrcode>
            <template #reference>
              <el-button>
                <SvgIcon name="handle-collect"/>
              </el-button>
            </template>
          </el-popover>
          <el-tooltip content="点击收藏" placement="bottom">
            <el-button @click="handleCollect">
              <SvgIcon name="handle-collect"/>
            </el-button>
          </el-tooltip>
          <el-button type="primary" @click="visitWebsite(websiteDetail.url)">
            立即访问
            <el-icon class="el-icon--right">
              <Right/>
            </el-icon>
          </el-button>
        </div>
      </div>
      <div class="detail-website-data">
        <div style="flex: 1"><span><el-icon><Place/></el-icon>&nbsp;分类：{{ websiteDetail.categoryName }}</span></div>
        <div style="flex: 1"><span><el-icon><View/></el-icon>&nbsp;浏览量：{{ websiteDetail.hitsNum || 0 }}</span></div>
        <div style="flex: 1"><span><el-icon><Star/></el-icon>&nbsp;收藏量：{{ websiteDetail.collectNum || 0 }}</span></div>
        <div style="flex: 1"><span><el-icon><CollectionTag/></el-icon>&nbsp;标签：{{
            (websiteDetail.tags || []).join("，")
          }}</span></div>
        <div style="flex: 1"><span><el-icon><Timer/></el-icon>&nbsp;收录时间：{{ websiteDetail.createTime }}</span></div>
      </div>
      <div class="detail-website-keywords" v-if="websiteDetail.keywords">
        <span>关键词：{{ websiteDetail.keywords }}</span>
      </div>
      <div class="detail-website-desc" v-if="websiteDetail.desc">
        <span>网站描述：{{ websiteDetail.desc }}</span>
      </div>

      <div class="detail-website-carousel" v-if="websiteDetail.screenshots">
        <el-carousel height="600px">
          <el-carousel-item v-for="screenshotUrl in websiteDetail.screenshots" :key="screenshotUrl">
            <el-image :src="screenshotUrl" style="width: 100%;">
              <div slot="error" class="image-slot">
                <img src="../../assets/error.jpg" width="100%"/>
              </div>
            </el-image>
          </el-carousel-item>
        </el-carousel>
      </div>

      <div class="detail-website-recommend"><span>同类网址</span></div>
      <BasicRow :list="websiteDetail.relationWebsites" v-if="websiteDetail.relationWebsites"/>
      <div v-else class="h-full flex justify-center items-center">
        <el-empty description="暂无数据"/>
      </div>

      <div class="detail-website-hint">
        <p class="detail-website-hint-title">特别提示</p>
        <p class="detail-website-hint-content">
          本网页并非“<i>{{ websiteDetail.name }}</i>”官网，页面内容是由【navigationline】编录于互联网，只作展示之用；如果有与“<i>{{
            websiteDetail.name
          }}</i>”相关业务事宜，请访问其网站并获取联系方式；【navigationline】与“<i>{{
            websiteDetail.name
          }}</i>”无任何关系，对于“<i>{{ websiteDetail.name }}</i>”网站中的信息，请用户谨慎辨识其真伪。
        </p>
      </div>
    </div>

    <Footer/>
  </div>
  <SimpleDialog
    v-model:visible="cloneMoveData.visible"
    :title="cloneMoveData.title"
    :footer="false"
  >
    <CloneMove isCollect :itemId="websiteDetail.id" @formResponse="handleCloneMoveRes"/>
  </SimpleDialog>
</template>

<script setup lang="ts">
import {useRouter} from 'vue-router'
import {ref} from 'vue'
import {fetchWebsiteDetail} from '@/api/fetchs/nav'
import {Right, Place, View, Star, CollectionTag, Timer} from '@element-plus/icons-vue'
import BasicRow from '@/components/BasicCard/BaiscRow.vue'
import Footer from '@/layout/default/footer/index.vue'
import VueQrcode from '@chenfengyuan/vue-qrcode'
import SimpleDialog from '@/components/SimpleDialog/SimpleDialog.vue'
import CloneMove from '@/components/ContentElement/clone-move/CloneMove.vue'
import {useCloneMoveForm} from '@/components/ContentElement/clone-move/useCloneMove'

const router = useRouter();

interface WebsiteInfo {
  id?: string,
  name?: string,
  url?: string,
  categoryName?: string,
  hitsNum?: number,
  collectNum?: number,
  createTime?: string,
  keywords?: string,
  desc?: string,
  logo?: string,
  tags?: [],
  screenshots?: [],
  relationWebsites?: []
}

const websiteDetail = ref<WebsiteInfo>({})

const {cloneMoveData, handleCloneMoveVisible, handleCloneMoveRes} = useCloneMoveForm('收藏到')

async function handleCollect() {
  handleCloneMoveVisible()
}

function visitWebsite(url){
  window.open(url)
}

/* ================================== fetchs ================================== */
async function getWebsiteDetail() {
  const {data} = await fetchWebsiteDetail({id: router.currentRoute.value.params.id || ''})

  websiteDetail.value = {
    id: data.id,
    name: data.name,
    url: data.url,
    categoryName: data.categoryName,
    hitsNum: data.hitsNum,
    collectNum: data.collectNum,
    createTime: data.createTime,
    keywords: data.keywords,
    desc: data.desc,
    logo: data.logo,
    tags: data.tags,
    screenshots: data.screenshots,
    relationWebsites: data.relationWebsites
  }
}

getWebsiteDetail()

</script>

